*, *::before, *::after,
*:before, *:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

ul, ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

.w--25 {
  width: 25%;
}
.w--50 {
  width: 50%;
}
.w--60 {
  width: 60%;
}
.w--65 {
  width: 65%;
}
.w--75 {
  width: 75%;
}
.w--80 {
  width: 80%;
}
.w--100 {
  width: 100%;
}

